import React from "react";
import Link from 'gatsby-link';

import styled from "styled-components";

import PostList from './../components/Blog/PostList.js';
import FadeIn from './../components/UI/animations/FadeIn.jsx';

const IndexPage = ({
  data,
}) => {
  const { allGamesJson } = data; // data.markdownRemark holds our post data
  const nodes = allGamesJson.edges;
  console.log("games nodes");
  console.log(nodes);
  const posts = nodes.map(node => {
    return <p key={node.node.id} >
    <Link key={node.node.id} to={node.node.path}>Post: {node.node.title}</Link>
    </p>
  })

  return (
  <FadeIn>
    <div>
            Games
        <PostList posts={nodes} />
    </div>
  </FadeIn>
  );
};

export default IndexPage;

export const pageQuery = graphql`
  query GamesIndexQuery {
    allGamesJson{
      edges{
        node{
          title,
          path,
          tags,
          game
        }
      }

    }
  }
`;





